<template>
  <div class="uploadimg">
    <div class="upload">
      <img
        src="../assets/uploadBtnIcon.png"
        alt=""
        @click="handleLaunchCamera()"
      />
    </div>
    <div class="camera" v-if="isCamera">
      <video
        ref="photo"
        webkit-playsinline="true"
        x-webkit-airplay="true"
        x5-playsinline="true"
        playsinline="true"
        id="videoCamera"
        :width="videoWidth"
        :height="videoHeight"
        autoplay
      ></video>
      <div class="shade_warp">
        <canvas
          style="display: none"
          :width="canvasWidth"
          :height="canvasHeight"
          id="canvasCamera"
        >
        </canvas>
        <img
          class="camera_border"
          src="../assets/icon-border.png"
          alt=""
        />
        <p class="camera_tips"></p>
        <div class="tools">
          <button id="photo"></button>
          <button class="suerCapture" @click="setImage()"></button>
          <button id="repCapture"></button>
          <button id="cancelCapture" @click="handleCloseCamera()"></button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isCamera: false, // 
      videoWidth: 300,
      videoHeight: 400,
      canvasWidth: 300,
      canvasHeight: 400,
      imgSrc: "",
      thisCancas: null,
      thisContext: null,
      thisVideo: null
    };
  },
  mounted() {},
  methods: {
    handleLaunchCamera() {
      this.isCamera = true;
      // 
      this.getCompetence();
    },
    handleCloseCamera() {
      this.isCamera = false;
    },
    getCompetence() {
      const _this = this;
      this.$nextTick(() => {
        // this.videoWidth = this.$refs.photo.clientWidth; //
        // this.videoHeight = this.videoWidth;
        this.thisCancas = document.getElementById("canvasCamera");
        this.thisContext = this.thisCancas.getContext("2d");
        this.thisVideo = document.getElementById("videoCamera");
      });
      // mediaDevices
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }
      // mediaDevices
      // getUserMedia
      // getUserMedia
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function(constraints) {
          // getUserMedia()
          var getUserMedia =
            navigator.webkitGetUserMedia ||
            navigator.mozGetUserMedia ||
            navigator.getUserMedia;
          // 
          // 
          if (!getUserMedia) {
            return Promise.reject(
              new Error("getUserMedia is not implemented in this browser")
            );
          }
          // Promisenavigator.getUserMedia
          return new Promise(function(resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        };
      }
      if (window.stream) {
        //
        window.stream.getTracks().forEach(track => {
          track.stop();
        });
      }
      var constraints = {
        //,
        audio: true,
        video: {
          width: this.videoWidth,
          height: this.videoHeight,
          transform: "scaleX(-1)",
          sourceId: "default",
          facingMode: { exact: "environment" }
          // facingMode: "user"
        }
      };
      var obj = {
        audio: true,
        video: { facingMode: { exact: "environment" } }
      };
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(stream) {
          // srcObject
          if ("srcObject" in _this.thisVideo) {
            _this.$nextTick(() => {
              _this.thisVideo.srcObject = stream; //that.videovideoupdated
              _this.thisVideo.play();
            });
          } else {
            // 
            _this.thisVideo.src = window.URL.createObjectURL(stream);
          }
          // _this.thisVideo.onloadedmetadata = function (e) {
          //   _this.thisVideo.play();
          // };
        })
        .catch(err => {
          console.log("");
          console.log(err);
        });
    },
    //  
    setImage() {
      const _this = this;
      // canvas
      _this.thisContext.scale(-1, 1); //
      this.thisContext.drawImage(
        _this.thisVideo,
        -_this.canvasWidth, //
        0,
        _this.canvasWidth,
        _this.canvasHeight
      );
      // base64
      const image = this.thisCancas.toDataURL("image/jpeg", 1);
      _this.imgSrc = image;
      console.log(image);
      const file = _this.dataURLtoFile(image, "pic");
      _this.upload(file);
      // 
      _this.thisVideo.pause();
      // this.stopNavigator();
    },
    // base64
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(",");
      var mime = arr[0].match(/:(.*?);/)[1];
      var bstr = atob(arr[1]);
      var n = bstr.length;
      var u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, {
        type: mime
      });
    }, // 
    upload(file) {
      this.showLoading = true;
      var formdata = new FormData();
      formdata.append("file", file, file.name);
      console.log("file");
      // this.axios.post("https://xxxxxxx/upload", formdata).then(res => {
      // });
    },
    // 
    stopNavigator() {
      this.thisVideo.srcObject.getTracks()[0].stop();
    }
  }
};
</script>
<style lang="less" scoped>
.uploadimg {
  .upload {
    img {
      width: 80px;
      height: 80px;
    }
  }
  .camera {
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: 99;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.9);
    #videoCamera {
      margin: 10px auto;
       display: block;
    }
    .shade_warp {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      #canvasCamera {
        margin: 10px auto;
      }
      .camera_border {
        display: block;
        width: 300px;
        height: 400px;
        margin: 10px auto;
      }
      .camera_tips {
        text-align: center;
        margin: 0;
        color: #fff;
        line-height: 1.5;
      }
      .tools {
        display: flex;
        justify-content: space-around;
        position: absolute;
        width: 100%;
        left: 0;
        bottom: 10px;
        button {
          border-radius: 50%;
          border: 1px solid #dcdcdc;
          width: 60px;
          height: 60px;
        }
      }
    }
  }
}
</style>



