<template>
  <div class="box">
    <el-row :span="24">
      <el-steps :active="active" align-center finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
    </el-row>
    <el-row :span="24">
      <el-form
        ref="elForm"
        :model="formData"
        :rules="rules"
        size="medium"
        label-width="100px"
        label-position="left"
      >
      <!---->
        <el-form-item label-width="120px" prop="picture1">
          <span slot="label">
            
          </span>
          <van-uploader
            v-model="picture1fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile1"
            :before-delete="removeFile1"
          />
        <div class="el-upload__tip"> {{ MBsize }}MB </div>
        </el-form-item>
<!--  -->
        <el-form-item label-width="120px" prop="picture2">
          <span slot="label">
            
          </span>
           <van-uploader
            v-model="picture2fileList"
            max-count="2"
            accept="image/*"
            :after-read="getFile2"
            :before-delete="removeFile2"
          />
         <div class="el-upload__tip"> {{ MBsize }}MB </div>
        </el-form-item>
        <!-- -->
        <el-form-item label-width="120px" prop="picture3">
          <span slot="label">
            
          </span>
           <van-uploader
            v-model="picture3fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile3"
            :before-delete="removeFile3"
          />
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="registerCode">
          <el-input
            v-model="formData.registerCode"
            placeholder=""
            clearable
            :style="{width: '100%'}"

          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="environmentalCode">
          <el-input
            v-model="formData.environmentalCode"
            placeholder=""
            clearable
            :style="{width: '100%'}"

          ></el-input>
        </el-form-item>
        <!-- -->
        <el-form-item label-width="120px" label="" prop="registerDate">
          <el-date-picker
            :editable="false"
            v-model="formData.registerDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{width: '100%'}"
            placeholder=""
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="productDate">
          <el-date-picker
            :editable="false"
            v-model="formData.productDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{width: '100%'}"
            placeholder=""
            clearable
          ></el-date-picker>
        </el-form-item>

        <el-form-item label-width="120px" label="/" prop="owner">
          <el-input
            v-model="formData.owner"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="mechanicType">
          <el-select
            v-model="formData.mechanicType"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          >
            <el-option
              v-for="(item, index) in mechanicTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="mechanicState">
          <el-select
            v-model="formData.mechanicState"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          >
            <el-option
              v-for="(item, index) in mechanicStateOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="emissionStageId">
          <el-select
            v-model="formData.emissionStageId"
            placeholder=""
            clearable
            :style="{width: '100%'}"
            @change="emissionStageIdChange(formData.emissionStageId)"
          >
            <el-option
              v-for="(item, index) in emissionOptions"
              :key="index"
              :label="item.emissionStageName"
              :value="item.emissionStageId"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="fuelType">
          <el-select
            v-model="formData.fuelType"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          >
            <el-option
              v-for="(item, index) in fuelTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>


        <el-form-item label-width="120px" label="" prop="remarks">
          <el-input
            type="textarea"
            v-model="formData.remarks"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>
        <el-form-item size="large">
          <el-button type="primary" @click="submitForm"></el-button>
          <el-button @click="resetForm"></el-button>
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>

<script>
import * as imageConversion from "image-conversion"

function getNum(value) {
  if (value.indexOf("+") != -1) {
    let num1 = parseInt(value.split("+")[0])
    let num2 = parseInt(value.split("+")[1])
    return num1 + num2
  } else {
    return value
  }
}
// let parkId = "";
// let accessToken = "";
// let userName = "";
//
export default {
  components: {},
  props: [],
  data() {
    return {
      parkId:'',
      accessToken:'',
      userName:'',
      MBsize: 18,
      active: 0,
      license1:null,
      license2:null,
      showStatus:false,
      formData: {
        picture1: "",
        picture2: "",
        picture3: "",
        machinePlatePhoto: undefined,//
        greenLablePhoto: undefined,//
        engineImageURL: undefined,//
        registerCode: undefined,//
        environmentalCode: undefined,//
        registerDate: null,//
        productDate: null,//
        owner: undefined,//
        mechanicType: undefined,//
        mechanicState: undefined,//
        emissionStageId: undefined,//Id
        fuelType:undefined,//
        remarks: undefined,//
      },
      rules: {
        picture3: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        picture1: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        picture2: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        registerCode: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        environmentalCode: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        registerDate: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        productDate:[
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        owner: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        mechanicType: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        mechanicState: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        emissionStageId: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        fuelType:[
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],

        remarks: [
          {
            required: false,
            message: "",
            trigger: "blur",
          },
        ],
      },
      picture1fileList: [],
      picture2fileList: [],
      picture3fileList: [],
      vehicleTypeOptions: [],
      fuelTypeOptions:[
        {value:"A",label:""},
        {value:"B",label:""},
        {value:"C",label:""},
        {value:"D",label:""},
        {value:"E",label:""},
        {value:"F",label:""},
        {value:"L",label:""},
        {value:"M",label:""},
        {value:"N",label:""},
        {value:"O",label:""},
        {value:"P",label:""},
        {value:"Q",label:""},
        {value:"Y",label:""},
        {value:"Z",label:""},
      ],
      mechanicTypeOptions:[
        {value:"1",label:""},
        {value:"2",label:""},
        {value:"3",label:""},
        {value:"4",label:""},
        {value:"5",label:""},
      ],
      mechanicStateOptions:[
        {value:"1",label:""},
        {value:"2",label:""},
        {value:"3",label:""},
      ],
      emissionOptions: [],
    };
  },
  
  watch: {
    formData: {
      handler: function () {
        this.$refs["elForm"].validate((valid) => {
          if (valid) {
            //
            this.active = 2;
          } else {
            if (this.formData.picture1 !== "") {
              //
              this.active = 1;
            } else {
              //
              this.active = 0;
            }
          }
        });
      },
      deep: true,
    },
  },
  mounted() {
    this.pageInit();
  },
  methods: {
    //
    emissionStageIdChange(value){
      switch (value) {
        case "0":
          this.showStatus = false
          break;
        case "1":
          this.showStatus = false
          break;
        case "2":
          this.showStatus = false
          break;
        case "3":
          this.showStatus = false
          break;
        case "4":
          this.showStatus = false
          break;
        case "5":
          this.showStatus = true
          break;
        case "6":
          this.showStatus = true
          break;
        case "7":
          this.showStatus = true
          break;
        case "D":
          this.showStatus = true
          break;
        case "X":
          this.showStatus = false
          break;

        default:
          break;
      }
    },
    //
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid){
          this.$alert('', '', {
            confirmButtonText: '',
          });
          return
        }
        if(this.license1!==this.license2){
          this.$alert('', '', {
            confirmButtonText: '',
          });
          return;
        }
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        })
          .then(() => {
            //
            //this.Axios.post(`openapi/dztz/qrcode/saveNoRoadMoveVehicle`
            this.Axios.post(`/openapi/dztz/qrcode/saveNoRoadMoveVehicle`, {
              parkId:this.parkId,
              accessToken:this.accessToken,
              userName:this.userName,
              machinePlatePhoto: this.formData.machinePlatePhoto,//
              greenLablePhoto: this.formData.greenLablePhoto,//
              engineImageURL: this.formData.engineImageURL,//
              registerCode: this.formData.registerCode,//
              environmentalCode: this.formData.environmentalCode,//
              registerDate: this.formData.registerDate,//
              productDate: this.formData.productDate,//
              owner: this.formData.owner,//
              mechanicType: this.formData.mechanicType,//
              mechanicState: this.formData.mechanicState,//
              emissionStageId: this.formData.emissionStageId,//Id
              fuelType:this.formData.machinePlatePhoto,//
              remarks: this.formData.remarks,//
            }).then((data) => {
              if (data.data.code == 200) {
                this.resetForm()
                this.$message({
                  showClose: true,
                  message: "",
                  type: "success",
                  center: true,
                  offset: 400,
                });
                //
                this.active = 3;

              }
            });
            // TODO 
          })
          .catch(() => {
            this.$message({
              showClose: true,
              message: "",
              type: "success",
              center: true,
              offset: 400,
            });
          });
      });
    },
    //
    resetForm() {
      this.$refs["elForm"].resetFields();
      //
      this.picture1fileList = [];
      this.picture2fileList = [];
      this.picture3fileList = [];
      //
      this.active = 0;
    },
    //blobbase64
    getBase64(blob, callback) {
      var a = new FileReader();
      a.onload = function (e) {
        callback(e.target.result);
      };
      a.readAsDataURL(blob);
    },
     //1
    removeFile1(file, fileList) {
      this.formData.picture1 = "";
      this.picture1fileList = [];
    },
      //2
    removeFile2(file, fileList) {
      this.formData.picture2 = "";
      this.picture2fileList = [];
    },
      //3
    removeFile3(file, fileList) {
      this.formData.picture3 = "";
      this.picture3fileList = [];
    },
    //1
    getFile1(file, fileList) {
      this.hideUploadEdit1 = fileList.length >= 2
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          }).then((data) => {
            // 
            if (this.formData.picture1 == "") {
              this.formData.picture1 = data.data.data;
            } else {
              this.formData.picture1 =
                this.formData.picture1 + "," + data.data.data;
            }
          });
        });
      });
    },
    //2
    getFile2(file, fileList) {
      this.hideUploadEdit2 = fileList.length >= 2
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          }).then((data) => {
            // 
            if (this.formData.picture2 == "") {
              this.formData.picture2 = data.data.data;
            } else {
              this.formData.picture2 =
                this.formData.picture2 + "," + data.data.data;
            }
          });
        });
      });
    },
    //3
    getFile3(file, fileList) {
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          }).then((data) => {
            // 
            if (this.formData.picture3 == "") {
              this.formData.picture3 = data.data.data;
            } else {
              this.formData.picture3 =
                this.formData.picture3 + "," + data.data.data;
            }
          });
        });
      });
    },
    pageInit() {
      this.parkId = this.$route.query.parkId;
      this.accessToken = this.$route.query.accessToken;
      this.userName = this.$route.query.userName;
      //
      this.Axios.post(`/openapi/dztz/getVehicleTypeList`, {
        parkId:this.parkId,
        accessToken:this.accessToken,
        userName:this.userName,
      }).then((data) => {
        this.vehicleTypeOptions = data.data.data;
      });
      //
      this.Axios.post(`/openapi/dztz/getEmissionStageList`, {
        parkId:this.parkId,
        accessToken:this.accessToken,
        userName:this.userName,
      }).then((data) => {
        this.emissionOptions = data.data.data;
      });
    },
  // num 1  2  3  4 
    ys(file, num) {
      return new Promise((resolve) => {
        let isLt2M = file.file.size / 1024 / 1024< 2;
        if (isLt2M) {
          resolve(file.file);
        } else {
          imageConversion
             .compressAccurately(file.file, 800*2)
            .then((res) => {
              resolve(res);
            });
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
.box {
  padding: 10px;
}
/deep/.hide .el-upload--picture-card {
  display: none;
}

.el-row {
  margin-bottom: 20px;
}
</style>



