<template>
  <div class="box">
    <el-row :span="24">
      <el-steps :active="active" align-center finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
    </el-row>
    <el-row :span="24">
      <el-form
        ref="elForm"
        :model="formData"
        :rules="rules"
        size="medium"
        label-width="100px"
        label-position="left"
      >
        <el-form-item label-width="120px" prop="">
            <span slot="label" style="line-height: 1.2">
            1()
            <p style="color: #f03a42; font-size: 12px; line-height: 1.2">
               
            </p>
          </span>
           <van-uploader
            v-model="picture1fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile1"
            :before-delete="removeFile1"
          />
          <div class="el-upload__tip"> {{ MBsize }}MB </div>
        </el-form-item>
        <el-form-item label-width="120px" prop="">
           <span slot="label">
            1()
           <p style="color: #f03a42; font-size: 12px; line-height: 1.2">
               
            </p>
          </span>
            <van-uploader
            v-model="picture3fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile3"
            :before-delete="removeFile3"
          />
            <div class="el-upload__tip"> {{ MBsize }}MB </div>
        </el-form-item>
        <el-form-item label-width="120px" prop="picture2" v-if="showStatus">
          <span slot="label">
            
          </span>
           <van-uploader
            v-model="picture2fileList"
            max-count="2"
            accept="image/*"
            :after-read="getFile2"
            :before-delete="removeFile2"
          />
          <div class="el-upload__tip"> {{ MBsize }}MB </div>
        </el-form-item>
        <el-form-item label-width="120px" label="/" prop="plateNumber">
          <el-input
            v-model="plateNumberComputed"
            placeholder=""
            clearable
            :style="{width: '100%'}"

          ></el-input>
        </el-form-item>
        <!-- -->
        <el-form-item label-width="120px" label="" prop="registerDate">
          <el-date-picker
            :editable="false"
            v-model="formData.registerDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{width: '100%'}"
            placeholder=""
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="productDate">
          <el-date-picker
            :editable="false"
            v-model="formData.productDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{width: '100%'}"
            placeholder=""
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="VIN" prop="vin">
          <el-input v-model="formData.vin" placeholder="VIN" clearable :style="{width: '100%'}"></el-input>
        </el-form-item>

        <el-form-item label-width="120px" prop="engineModel">
          <span slot="label">
            <span>
              <span></span>
              <el-tooltip placement="top"></el-tooltip>
            </span>
          </span>
          <el-input
            v-model="formData.engineModel"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="registerCode">
          <el-input
            v-model="formData.registerCode"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="owner">
          <el-input
            v-model="formData.owner"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="emissionStageId">
          <el-select
            v-model="formData.emissionStageId"
            placeholder=""
            clearable
            :style="{width: '100%'}"
            @change="emissionStageIdChange(formData.emissionStageId)"
          >
            <el-option
              v-for="(item, index) in emissionOptions"
              :key="index"
              :label="item.emissionStageName"
              :value="item.emissionStageId"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="fuelType">
          <el-select
            v-model="formData.fuelType"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          >
            <el-option
              v-for="(item, index) in fuelTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <div v-if="this.accessPlatform == 22">
          <el-form-item label-width="120px" label="OBD" prop="isobd">
            <el-radio-group v-model="formData.isobd">
              <el-radio label="1"></el-radio>
              <el-radio label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label-width="120px" label="OBD" prop="obdTime" v-if="this.formData.isobd == '1'">
            <el-col>
              <el-date-picker
                type="date"
                placeholder=""
                v-model="formData.obdTime"
                style="width: 100%;"
              >
              </el-date-picker>
            </el-col>
          </el-form-item>
        </div>


        <el-form-item label-width="120px" label="" prop="remarks">
          <el-input
            type="textarea"
            v-model="formData.remarks"
            placeholder=""
            clearable
            :style="{width: '100%'}"
          ></el-input>
        </el-form-item>
        <el-form-item size="large">
          <el-button type="primary" @click="submitForm"></el-button>
          <el-button @click="resetForm"></el-button>
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>

<script>
import * as imageConversion from "image-conversion"

function getNum(value) {
  if (value.indexOf("+") != -1) {
    let num1 = parseInt(value.split("+")[0])
    let num2 = parseInt(value.split("+")[1])
    return num1 + num2
  } else {
    return value
  }
}

//
// const quryToObj = (url) => {
//   var params = url.split("?")[1].split("&");
//   var obj = {};
//   params.map((v) => (obj[v.split("=")[0]] = v.split("=")[1]));
//   return obj;
// };
export default {
  components: {},
  props: [],
  data() {
    return {
      parkId : '',
      accessToken : '',
      accessPlatform:'',
      userName : '',
      MBsize: 18,
      active: 0,
      license1:null,
      license2:null,
      showStatus:false,
      formData: {
        picture1: "",
        picture2: "",
        picture3: "",
         //goodsListURL,//
        // licenseURL//
        // gatewayId//
        isobd:'',
        obdTime:'',
        productDate:undefined,//
        plateNumber: undefined,//
        registerDate: null,//
        vin: undefined,//VIN
        owner: undefined,//
        registerCode:'',//
        emissionStageId: undefined,//
        fuelType:undefined,//
        engineModel: undefined,//
        remarks: undefined,//
      },
      rules: {
        plateNumber: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        registerDate: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        productDate: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        vin: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        registerCode: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        isobd:[
          {
            required: true,
            message: "OBD",
            trigger: "blur",
          }
        ],
        obdTime:[
          {
            required: true,
            message: "OBD",
            trigger: "blur",
          }
        ],
        owner: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        emissionStageId: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        fuelType:[
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        engineModel: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        remarks: [
          {
            required: false,
            message: "",
            trigger: "blur",
          },
        ],
      },
      picture1fileList: [],
      picture2fileList: [],
      picture3fileList: [],
      fuelTypeOptions:[
        {value:"A",label:""},
        {value:"B",label:""},
        {value:"C",label:""},
        {value:"D",label:""},
        {value:"E",label:""},
        {value:"F",label:""},
        {value:"L",label:""},
        {value:"M",label:""},
        {value:"N",label:""},
        {value:"O",label:""},
        {value:"P",label:""},
        {value:"Q",label:""},
        {value:"Y",label:""},
        {value:"Z",label:""},
      ],
      emissionOptions: [],
      // wxtip:false
    };
  },
  computed: {
    //
    plateNumberComputed: {
      get: function () {
        return this.formData.plateNumber;
      },
      set: function (val) {
        this.formData.plateNumber = val.toUpperCase();
      },
    },
  },
  watch: {
    formData: {
      handler: function () {
        this.$refs["elForm"].validate((valid) => {
          if (valid) {
            //
            this.active = 2;
          } else {
            if (this.formData.picture1 !== "") {
              //
              this.active = 1;
            } else {
              //
              this.active = 0;
            }
          }
        });
      },
      deep: true,
    },
  },
  created() {},
  mounted() {
    // let ua = navigator.userAgent.toLowerCase();
    // let isWeixin = ua.indexOf('micromessenger') != -1;
    // if (isWeixin) {
    //   this.wxtip = true
    // }else{
    // }
    this.pageInit();
  },
  methods: {
    //
    emissionStageIdChange(value){
      switch (value) {
        case "0":
          this.showStatus = false
          break;
        case "1":
          this.showStatus = false
          break;
        case "2":
          this.showStatus = false
          break;
        case "3":
          this.showStatus = false
          break;
        case "4":
          this.showStatus = false
          break;
        case "5":
          this.showStatus = true
          break;
        case "6":
          this.showStatus = true
          break;
        case "7":
          this.showStatus = true
          break;
        case "D":
          this.showStatus = true
          break;
        case "X":
          this.showStatus = false
          break;

        default:
          break;
      }
    },
    //
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid){
          this.$alert('', '', {
            confirmButtonText: '',
          });
          return
        }
        if(this.license1!==this.license2){
          this.$alert('', '', {
            confirmButtonText: '',
          });
          return;
        }
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        })
          .then(() => {
            //
            //this.Axios.post(`openapi/dztz/qrcode/saveOnSideTransportVehicle`
            this.Axios.post(`/openapi/dztz/qrcode/saveOnSideTransportVehicle`, {
              parkId:this.parkId,
              accessToken:this.accessToken,
              userName:this.userName,
              plateNumber: this.formData.plateNumber,//
              registerDate: this.formData.registerDate,//
              productDate: this.formData.productDate,//
              vin: this.formData.vin,//VIN
              engineModel: this.formData.engineModel,//
              emissionStageId: this.formData.emissionStageId,//
              fuelType: this.formData.fuelType,//
              isobd:this.formData.isobd,
              obdTime:this.formData.obdTime,
              owner: this.formData.owner,//
              registerCode:this.formData.registerCode,//
              licenseURL: `${this.formData.picture1},${this.formData.picture3}`,//URL
              goodsListURL: this.formData.picture2,//URL
              remarks: this.formData.remarks,//
            }).then((data) => {
              if (data.data.code == 200) {
                this.resetForm()
                this.$message({
                  showClose: true,
                  message: "",
                  type: "success",
                  center: true,
                  offset: 400,
                });
                //
                this.active = 3;

              }
            });
            // TODO 
          })
          .catch(() => {
            this.$message({
              showClose: true,
              message: "",
              type: "success",
              center: true,
              offset: 400,
            });
          });
      });
    },
    //
    resetForm() {
      this.$refs["elForm"].resetFields();
      //

      this.picture1fileList = [];
      this.picture2fileList = [];
      this.picture3fileList = [];
      //
      this.active = 0;
    },
    //blobbase64
    getBase64(blob, callback) {
      var a = new FileReader();
      a.onload = function (e) {
        callback(e.target.result);
      };
      a.readAsDataURL(blob);
    },
    //1
    getFile1(file, fileList) {
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/ocrDrivingLicense`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          })
            .then((data) => {
              this.license1 = data.data.data.plateNumber;
              let date = data.data.data.registerDate?data.data.data.registerDate:"";
              let date2 = data.data.data.productDate?data.data.data.productDate:"";
              if(date!=""&&new RegExp(/\d{4}\d{2}\d{2}/).test(date)){
                this.formData.registerDate =
                  date.slice(0, 4) +
                  "-" +
                  date.slice(4, 6) +
                  "-" +
                  date.slice(6, 8);
              }
              if(date2!=""&&new RegExp(/\d{4}\d{2}\d{2}/).test(date2)){
                this.formData.productDate =
                  date2.slice(0, 4) +
                  "-" +
                  date2.slice(4, 6) +
                  "-" +
                  date2.slice(6, 8);
              }
              //
              this.formData.plateNumber = data.data.data.plateNumber;
              //
              this.formData.vin = data.data.data.vin;
              // id id
              (this.formData.vehicleTypeId = this.vehicleTypeOptions[
                this.vehicleTypeOptions
                  .map((item) => item.vehicleTypeName)
                  .indexOf(data.data.data.vehicleTypeName)
                ].vehicleTypeId),
                //
                (this.formData.owner = data.data.data.owner);
              //
              this.formData.picture1 = data.data.data.imageURL;
              //
              this.formData.engineModel = data.data.data.engineModel;
              this.formData.address = data.data.data.address;
              //
              this.active = 1;
            })
        });
      });
    },
    //1
    removeFile1(file, fileList) {
       this.picture1fileList = [];
      //
      this.active = 0;
    },
    //3
    getFile3(file, fileList) {
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/ocrDrivingLicense2`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          })
            .then((data) => {
              this.license2 = data.data.data.license;
              this.formData.approvedLoad = data.data.data.approvedLoad.split("kg")[0]
              this.formData.appprovedPassengerCapacity = getNum(data.data.data.appprovedPassengerCapacity.split("")[0]);
              this.formData.picture3 = data.data.data.imageURL;
              this.active = 1;
            })
            .then(() => {
              //
              //this.getCarInfo();
            });
        });
      });
    },
    //1
    removeFile3(file, fileList) {
       this.picture3fileList = [];
      this.active = 0;
    },
    //2
    getFile2(file, fileList) {
      this.ys(file).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId:this.parkId,
            accessToken:this.accessToken,
            userName:this.userName,
            image: res,
          }).then((data) => {
            // 
            if (this.formData.picture2 == "") {
              this.formData.picture2 = data.data.data;
            } else {
              this.formData.picture2 =
                this.formData.picture2 + "," + data.data.data;
            }
          });
        });
      });
    },
    //2
    removeFile2(file, fileList) {
      this.formData.picture2 = "";
      this.picture2fileList = [];
    },
    pageInit() {
      // let testUrl =
      //   "http://park.leting360.cn/app/index.html#?parkId=2628ee995d6da9769f8e5ae9dc3d10a7&accessToken=41040300140120110600001&userName=TEST#/";
      // console.log(window.location.href, "url");
      // let url = window.location.href;
      // //url
      // let urlData = quryToObj(url.split('#/')[0]);
      // parkId = urlData.parkId;
      // accessToken = urlData.accessToken;
      // userName = urlData.userName;
      // console.log(urlData, "");
      this.parkId = this.$route.query.parkId;
      this.accessToken = this.$route.query.accessToken;
      this.userName = this.$route.query.userName;
      this.accessPlatform = this.$route.query.accessPlatform;
      //
      this.Axios.post(`/openapi/dztz/getVehicleTypeList`, {
        parkId:this.parkId,
        accessToken:this.accessToken,
        userName:this.userName,
      }).then((data) => {
        this.vehicleTypeOptions = data.data.data;
      });
      //
      this.Axios.post(`/openapi/dztz/getEmissionStageList`, {
        parkId:this.parkId,
        accessToken:this.accessToken,
        userName:this.userName,
      }).then((data) => {
        this.emissionOptions = data.data.data;
      });
    },
    // num 1  2  3  4 
    ys(file, num) {
      return new Promise((resolve) => {
        let isLt2M = file.file.size / 1024 / 1024< 2;
        if (isLt2M) {
          resolve(file.file);
        } else {
          imageConversion
             .compressAccurately(file.file, 800*2)
            .then((res) => {
              resolve(res);
            });
        }
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.box {
  padding: 10px;
}
/deep/.hide .el-upload--picture-card {
  display: none;
}

.el-row {
  margin-bottom: 20px;
}
</style>



