<template>
  <div class="uploadimg">
    <div class="uploadFacePic">
      <!--  -->
      <!-- -->
      <main style="margintop: 20px">
        <div class="btn" @click="openCamera"></div>
      </main>
      <!-- 1  -->
      <input
        id="file"
        type="file"
        accept="image/*"
        capture="camera"
        style="display: none"
      />
      <!-- 2  -->
      <div
        style="
          width: 100%;
          position: fixed;
          left: 0;
          bottom: 0;
          top: 0;
          right: 0;
        "
        v-if="cameraShow"
      >
        <!--  -->
        <div
          style="
            width: 100%;
            position: fixed;
            left: 0;
            bottom: 90vh;
            top: 0;
            right: 0;
            background: black;
          "
        ></div>
        <!--  -->
        <video
          style="
            height: 65vh;
            width: 100vw;
            position: fixed;
            top: 10vh;
            left: 0;
          "
        ></video>
        <div
          style="
            width: 100%;
            position: fixed;
            left: 0;
            bottom: 25vh;
            top: 10vh;
            right: 0;
          "
        >
          <!-- a. -->
          <img
            src="../assets/icon-border.png"
            alt=""
            v-if="status == 1"
            style="width: 100%; height: 100%; opacity: 0.8"
          />
          <!-- b. -->
          <img
            :src="imageUrl"
            alt=""
            v-if="status == 2"
            style="width: 100%; height: 100%"
          />
        </div>
        <!-- -->
        <div class="control">
          <!--  -->
          <div class="control_before" v-if="status == 1">
            <div class="control_before_top"></div>
            <div class="control_before_bottom">
              <div class="smaller" @click="cameraShow = false"></div>
              <i class="iconfont icon-xiangji bigger" @click="snapPhoto"></i>
              <i
                class="iconfont icon-zhongxin small"
                @click="front = !front"
              ></i>
            </div>
          </div>
          <!--  -->
          <div class="control_after" v-if="status == 2">
            <div class="smaller" @click="status = 1"></div>
            <div class="smaller" @click="submitPhoto('custom')"></div>
          </div>
        </div>
        <!--  -->
        <canvas id="mycanvas"></canvas>
      </div>
      <!--  -->
      <div class="tipinfo" v-if="tipVisible">
        <div class="successContent" v-if="tipInfo.result == 'ok'">
          <van-icon name="passed" />
          <div class="title"></div>
          <div class="info"></div>
          <div class="btn" @click="tipVisible = false">
            {{ "" + btntext }}
          </div>
        </div>
        <div class="failContent" v-else>
          <van-icon name="warning-o" />
          <div class="title"></div>
          <div class="info">{{ tipInfo.msg + "," }}</div>
          <div class="btn" @click="tipVisible = false">
            {{ "" + btntext }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    limitCount: {
      type: Number,
      default: 1
    },
    limitSize: {
      type: Number,
      default: 2
    }
  },
  data() {
    return {
      type: "", // update|upload
      cameraShow: false, //
      status: 0, //-:0| 1| 2|
      imageUrl: "", //-url
      front: true, // -

      //
      tipVisible: false,
      tipInfo: {
        result: "fail",
        msg: ""
      }, //
      btntext: "", //
      time: null, //

      imageFile: "" //
    };
  },
  methods: {
    openCamera() {
      // 1. video
      this.cameraShow = true;

      // 2. constraints:
      var constraints = {
        audio: false,
        video: {
          facingMode: this.front ? "user" : "environment"
        }
      };

      // 3. 
      //  mediaDevices
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }
      //  mediaDevices getUserMedia
      // getUserMedia
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function(constraints) {
          // getUserMedia
          var getUserMedia =
            navigator.getUserMedia ||
            navigator.webkitGetUserMedia ||
            navigator.mozGetUserMedia ||
            navigator.msGetUserMedia ||
            navigator.oGetUserMedia;
          //  - errorpromisereject
          if (!getUserMedia) {
            return Promise.reject(
              new Error("getUserMedia is not implemented in this browser")
            );
          }
          // navigator.getUserMediaPromise
          return new Promise(function(resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        };
      }
      // 4. 
      let that = this;
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(stream) {
          // 
          let video = document.querySelector("video");
          video.srcObject = stream;
          video.onloadedmetadata = function(e) {
            video.play();
          };
          // 
          that.status = 1;
        })
        .catch(function(err) {
          // 
          console.log("nonono", err);
          // 
          that.originCamera();
        });
    },
    originCamera() {
      let that = this;
      //
      that.cameraShow = false;
      let promise = new Promise(function(resolve, reject) {
        let file = document.getElementById("file");
        file.click();
        file.onchange = function(event) {
          if (!event) {
            reject("empty");
          }
          //>
          let file = event.target.files[0];
          resolve(file);
        };
      });
      promise.then(value => {
        that.submitPhoto("origin", value);
      });
    },
    submitPhoto(type, file) {
      if (type == "origin") {
        this.imageFile = file;
      }
      console.log("", this.imageFile);
      //
      // let fd=new FormData()
      // fd.append("face_image",this.imageFile)
      //
      this.tipInfo.result = "ok";
      // this.tipInfo.result='fail'
      this.cameraShow = false;
      //
      this.countdown();
    },
    //
    countdown() {
      clearInterval(this.time);
      this.tipVisible = true;
      let coden = 3;
      this.btntext = "(" + coden + "s)";
      this.time = setInterval(() => {
        coden--;
        this.btntext = "(" + coden + "s)";
        if (coden == 0) {
          clearInterval(this.time);
          this.tipVisible = false;
          this.btntext = "";
        }
      }, 1000);
    }
  }
};
</script>

<style scoped></style>



