
<template>
  <div>
    <el-button @click="onTake" icon="el-icon-camera" size="small"></el-button>

    <el-row>
      <el-col :span="24">
        <el-dialog :visible.sync="visible" @close="onCancel" fullscreen>
          <div>
            <video
              v-show="isShow1"
              id="videoCamera"
              :width="videoWidth"
              :height="videoHeight"
              autoplay
            ></video>
            <canvas v-show="isShow2" id="canvasCamera" :width="videoWidth" :height="videoHeight"></canvas>
          </div>
          <div slot="footer">
            <el-button @click="drawImage" icon="el-icon-camera" size="small"></el-button>
            <!-- <el-button
              v-if="open"
              @click="getCompetence"
              icon="el-icon-video-camera"
              size="small"
            ></el-button> -->
            <!-- <el-button v-else @click="stopNavigator" icon="el-icon-switch-button" size="small"></el-button> -->
            <el-button @click="resetCanvas" icon="el-icon-refresh" size="small"></el-button>
            <el-button @click="onCancel" icon="el-icon-circle-close" size="small"></el-button>
            <el-button
              @click="onUpload"
              :loading="loading"
              type="primary"
              icon="el-icon-upload2"
              size="small"
            ></el-button>
          </div>
        </el-dialog>
      </el-col>
    </el-row>
  </div>
</template>
 
<script>
let x = document.documentElement.clientWidth; //
let y = document.documentElement.clientHeight; //
//
// getImg , @getImg="getImg"
const Address = "http://datatest.leting360.cn/openapi/dztz/ocrDrivingLicense"; //
export default {
  name: "TakePhotos",
  data() {
    return {
      isShow1:true,
      isShow2:false,
      imgSrc: "",
      visible: false, //
      loading: false, //
      open: false, //
      thisVideo: null,
      thisContext: null,
      thisCancas: null,
      videoWidth: x,
      videoHeight: y - 100,
    };
  },
  methods: {
    onTake() {
      this.visible = true;
      this.getCompetence();
    },
    onCancel() {
      this.visible = false;
      this.resetCanvas();
      this.stopNavigator();
    },
    //base64
    onUpload() {
      if (this.imgSrc) {
        const file = this.imgSrc; // base64file
        const time = new Date().valueOf(); //
        const name = time + ".png"; // abc.png  cover.png
        const conversions = this.dataURLtoFile(file, name); // base64
        const data = new FormData();
        data.append("file", conversions);
        const options = {
          method: "POST", //
          body: data, //
          headers: {
            Accept: "application/json",
          },
        };
        this.loading = true;
        fetch(Address.UPLOAD, options)
          .then((response) => {
            return response.json();
          })
          .then((responseText) => {
            this.loading = false;
            if (responseText.code === 0) {
              this.imgSrc = responseText.data.src;
              this.$emit("getImg", responseText.data.src); //
              this.onCancel();
              this.$notify({
                title: "",
                message: responseText.msg,
                type: "success",
              });
            }
          })
          .catch((error) => {
            this.loading = false;
            this.$notify.error({
              title: "",
              message: error.msg,
            });
          });
      } else {
        this.$notify({
          title: "",
          message: "",
          type: "warning",
        });
      }
    },
    // 
    getCompetence() {
      //modelrenderdom,modeldom
      this.$nextTick(() => {
        const _this = this;
        this.open = false; //
        this.thisCancas = document.getElementById("canvasCamera");
        this.thisContext = this.thisCancas.getContext("2d");
        this.thisVideo = document.getElementById("videoCamera");
        // mediaDevices
        if (navigator.mediaDevices === undefined) {
          navigator.mediaDevices = {};
        }
        // mediaDevices
        // getUserMedia
        // getUserMedia
        if (navigator.mediaDevices.getUserMedia === undefined) {
          navigator.mediaDevices.getUserMedia = function (constraints) {
            // getUserMedia()
            let getUserMedia =
              navigator.webkitGetUserMedia ||
              navigator.mozGetUserMedia ||
              navigator.getUserMedia;
            // 
            // 
            if (!getUserMedia) {
              return Promise.reject(
                new Error("getUserMedia is not implemented in this browser")
              );
            }
            // Promisenavigator.getUserMedia
            return new Promise(function (resolve, reject) {
              getUserMedia.call(navigator, constraints, resolve, reject);
            });
          };
        }
        const constraints = {
          audio: false,
          video: {
            width: _this.videoWidth,
            height: _this.videoHeight,
            transform: "scaleX(-1)",
          },
        };
        navigator.mediaDevices
          .getUserMedia(constraints)
          .then(function (stream) {
            // srcObject
            if ("srcObject" in _this.thisVideo) {
              _this.thisVideo.srcObject = stream;
            } else {
              // 
              _this.thisVideo.src = window.URL.createObjectURL(stream);
            }
            _this.thisVideo.onloadedmetadata = function (e) {
              _this.thisVideo.play();
            };
          })
          .catch((err) => {
            console.log(err, 11111111111111111);
            this.$notify({
              title: "",
              message: ".",
              type: "warning",
            });
          });
      });
    },
    //
    drawImage() {
      // canvas
      this.thisContext.drawImage(
        this.thisVideo,
        0,
        0,
        this.videoWidth,
        this.videoHeight
      );
      // base64
      this.imgSrc = this.thisCancas.toDataURL("image/png");
      this.isShow1=false;
      this.isShow2=true;
    },
    //base64
    dataURLtoFile(dataurl, filename) {
      let arr = dataurl.split(",");
      let mime = arr[0].match(/:(.*?);/)[1];
      let bstr = atob(arr[1]);
      let n = bstr.length;
      let u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    },
    //
    clearCanvas(id) {
      let c = document.getElementById(id);
      let cxt = c.getContext("2d");
      cxt.clearRect(0, 0, c.width, c.height);
    },
    //
    resetCanvas() {
      this.imgSrc = "";
      this.clearCanvas("canvasCamera");
      this.isShow1 = true;
      this.isShow2 = false;
    },
    //
    stopNavigator() {
      if (this.thisVideo && this.thisVideo !== null) {
        this.thisVideo.srcObject.getTracks()[0].stop();
        this.open = true; //
      }
    },
  },
  beforeDestroy() {
    this.stopNavigator();
  },
};
</script>
 
<style scoped lang="less">
/deep/.el-dialog.is-fullscreen {
  background: black;
}
/deep/.el-dialog__body {
  padding: 0;
  margin-top: 10px;
  margin-bottom: 10px;
}
/deep/.el-dialog__footer{
  padding: 0;
  text-align: center;
}
</style>


