<template>
  <div class="box">
    <el-row :span="24">
      <el-steps :active="active" align-center finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
    </el-row>
    <el-row :span="24">
      <el-form
        ref="elForm"
        :model="formData"
        :rules="rules"
        size="medium"
        label-width="100px"
        label-position="left"
      >
        <el-form-item label-width="120px" prop="picture1">
          <span slot="label" style="line-height: 1.2">
            1()
            <p style="color: #f03a42; font-size: 12px; line-height: 1.2">
               
            </p>
          </span>
          <!-- <Myuploadimg/> -->
          <MyuploadimgCase/>
          <!-- <van-uploader
            v-model="picture1fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile1"
            :before-delete="removeFile1"
          />
          <div class="el-upload__tip"> {{ MBsize }}MB </div> -->
        </el-form-item>
        <el-form-item label-width="120px" prop="picture3">
          <span slot="label">
            1()
            <p style="color: #f03a42; font-size: 12px;line-height: 1.2">
               
            </p>
          </span>
          <van-uploader
            v-model="picture3fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile3"
            :before-delete="removeFile3"
          />
          <div class="el-upload__tip"> {{ MBsize }}MB </div>
        </el-form-item>

        <el-form-item
          label-width="120px"
          prop="picture4"
          v-if="this.accessPlatform == 22"
        >
          <span slot="label">  </span>
          <van-uploader
            v-model="picture4fileList"
            max-count="1"
            accept="image/*"
            :after-read="getFile4"
            :before-delete="removeFile4"
          />
          <div class="el-upload__tip"> {{ MBsize }}MB </div>
        </el-form-item>
        <el-form-item label-width="120px" prop="picture2">
          <span slot="label">  </span>
          <van-uploader
            v-model="picture2fileList"
            max-count="2"
            accept="image/*"
            :after-read="getFile2"
            :before-delete="removeFile2"
          />
          <div class="el-upload__tip"> {{ MBsize }}MB </div>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="plateNumber">
          <el-input
            v-model="plateNumberComputed"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="vehicleModel">
          <el-input
            v-model="formData.vehicleModel"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label-width="120px" label="" prop="useCharacter">
          <el-input
            v-model="formData.useCharacter"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item> -->
         <el-form-item label-width="120px"  label="" prop="useCharacter">
          <el-select 
          v-model.number="formData.useCharacter"
           v-model="formData.vehicleModel"
            placeholder=""
            clearable
            :style="{ width: '100%' }">
           <el-option
              v-for="item in selectOptions.useCharacterOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
                  >
           </el-option>
            </el-select>
        </el-form-item> 
        <el-form-item label-width="120px" label="" prop="registerDate">
          <el-date-picker
            :editable="false"
            v-model="formData.registerDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{ width: '100%' }"
            placeholder=""
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="issueDate">
          <el-date-picker
            :editable="false"
            v-model="formData.issueDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            :style="{ width: '100%' }"
            placeholder=""
            clearable
          ></el-date-picker>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="vin">
          <el-input
            v-model="formData.vin"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="vehicleTypeId">
          <el-select
            filterable
            v-model="formData.vehicleTypeId"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          >
            <el-option
              v-for="(item, index) in vehicleTypeOptions"
              :key="index"
              :label="item.vehicleTypeName"
              :value="item.vehicleTypeId"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="owner">
          <el-input
            v-model="formData.owner"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>

        <el-form-item
          label-width="120px"
          label=""
          prop="emissionStageId"
        >
          <el-select
            v-model="formData.emissionStageId"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
            @change="emissionStageIdChange(formData.emissionStageId)"
          >
            <el-option
              v-for="(item, index) in emissionOptions"
              :key="index"
              :label="item.emissionStageName"
              :value="item.emissionStageId"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" label="" prop="fuelType">
          <el-select
            v-model="formData.fuelType"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          >
            <el-option
              v-for="(item, index) in fuelTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label-width="120px" prop="engineModel">
          <span slot="label">
            <span>
              <span></span>
              <el-tooltip placement="top"></el-tooltip>
            </span>
          </span>
          <el-input
            v-model="formData.engineModel"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="plateColor">
          <el-select
            v-model="formData.plateColor"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          >
            <el-option
              v-for="(item, index) in plateColorOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="grossMass">
          <el-input
            v-model="formData.grossMass"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          >
            <template slot="append">KG</template>
          </el-input>
        </el-form-item>
        <el-form-item
          label-width="120px"
          label=""
          prop="approvedLoad"
        >
          <el-input
            v-model="formData.approvedLoad"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          >
            <template slot="append">KG</template>
          </el-input>
        </el-form-item>
        <el-form-item
          label-width="120px"
          label=""
          prop="appprovedPassengerCapacity"
        >
          <el-input
            v-model="formData.appprovedPassengerCapacity"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          >
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item
          label-width="120px"
          label="OBD"
          prop="isobd"
          v-if="this.accessPlatform == 22"
        >
          <el-radio-group v-model="formData.isobd">
            <el-radio label="1"></el-radio>
            <el-radio label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="address">
          <el-input
            v-model="formData.address"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item label-width="120px" label="" prop="remarks">
          <el-input
            type="textarea"
            v-model="formData.remarks"
            placeholder=""
            clearable
            :style="{ width: '100%' }"
          ></el-input>
        </el-form-item>
        <el-form-item size="large">
          <el-button type="primary" @click="submitForm"></el-button>
          <el-button @click="resetForm"></el-button>
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>

<script>
import * as imageConversion from "image-conversion";

import Myuploadimg from "./uploadimg.vue";
import MyuploadimgCase from "./uploadimgCase.vue";

function getNum(value) {
  if (value.indexOf("+") != -1) {
    let num1 = parseInt(value.split("+")[0]);
    let num2 = parseInt(value.split("+")[1]);
    return num1 + num2;
  } else {
    return value;
  }
}
export default {
  components: {Myuploadimg,MyuploadimgCase},
  data() {
    return {
      selectOptions: {
        useCharacterOptions: [
          {
            value: "A",
            label: "",
          },
          {
            value: "B",
            label: "",
          },
          {
            value: "C",
            label: "",
          },
          {
            value: "D",
            label: "",
          },
          {
            value: "E",
            label: "",
          },
          {
            value: "F",
            label: "",
          },
          {
            value: "G",
            label: "",
          },
          {
            value: "H",
            label: "",
          },
          {
            value: "I",
            label: "",
          },
          {
            value: "J",
            label: "",
          },
          {
            value: "K",
            label: "",
          },
          {
            value: "L",

            label: "",
          },
          {
            value: "M",

            label: "",
          },
          {
            value: "N",

            label: "",
          },
          {
            value: "O",

            label: "",
          },
          {
            value: "P",

            label: "",
          },
          {
            value: "Q",

            label: "",
          },
          {
            value: "R",

            label: "",
          },
          {
            value: "S",

            label: "",
          },
        ],
      },
      parkId: "",
      accessToken: "",
      accessPlatform: "",
      userName: "",
      MBsize: 2,
      active: 0,
      license1: null,
      license2: null,
      showStatus: false,
      formData: {
        isobd: "2",
        picture1: "",
        picture2: "",
        picture3: "",
        picture4: "",
        plateNumber: undefined,
        registerDate: null,
        issueDate: null,
        vin: undefined,
        vehicleTypeId: undefined,
        vehicleModel: undefined, //
        useCharacter: undefined, //
        owner: undefined,
        emissionStageId: undefined,
        fuelType: undefined,
        engineModel: undefined,
        plateColor: undefined,
        grossMass: undefined,
        approvedLoad: undefined,
        appprovedPassengerCapacity: undefined,
        address: undefined,
        remarks: undefined,
      },
      rules: {
        picture1: [
          {
            required: true,
            message: "()",
            trigger: "blur",
          },
        ],
        picture4: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        picture3: [
          {
            required: true,
            message: "()",
            trigger: "blur",
          },
        ],
        picture2: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        plateNumber: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        vehicleModel: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ], //
        useCharacter: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ], //
        registerDate: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        issueDate: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        vin: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        vehicleTypeId: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        owner: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        emissionStageId: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        fuelType: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        engineModel: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        plateColor: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        grossMass: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { pattern: /^[1-9]\d*$/, message: "" },
        ],
        approvedLoad: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { pattern: /^[1-9]\d*$/, message: "" },
        ],
        appprovedPassengerCapacity: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { pattern: /^[+]{0,1}(\d+)$/, message: "" },
        ],
        isobd: [
          {
            required: true,
            message: "OBD",
            trigger: "blur",
          },
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        remarks: [
          {
            required: false,
            message: "",
            trigger: "blur",
          },
        ],
      },
      picture1fileList: [],
      picture2fileList: [],
      picture3fileList: [],
      picture4fileList: [],
      vehicleTypeOptions: [],
      plateColorOptions: [
        { value: "0", label: "" },
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" },
        { value: "4", label: "" },
        { value: "5", label: "" },
      ],
      fuelTypeOptions: [
        { value: "A", label: "" },
        { value: "B", label: "" },
        { value: "C", label: "" },
        { value: "D", label: "" },
        { value: "E", label: "" },
        { value: "F", label: "" },
        { value: "L", label: "" },
        { value: "M", label: "" },
        { value: "N", label: "" },
        { value: "O", label: "" },
        { value: "P", label: "" },
        { value: "Q", label: "" },
        { value: "Y", label: "" },
        { value: "Z", label: "" },
      ],
      emissionOptions: [],
    };
  },
  computed: {
    //
    plateNumberComputed: {
      get: function () {
        return this.formData.plateNumber;
      },
      set: function (val) {
        this.formData.plateNumber = val.toUpperCase();
      },
    },
  },
  watch: {
    formData: {
      handler: function () {
        if (this.formData.picture1) {
          //
          this.active = 1;
        } else {
          //
          this.active = 0;
        }
      },
      deep: true,
    },
  },
  mounted() {
    this.pageInit();
  },
  methods: {
    //
    emissionStageIdChange(value) {
      switch (value) {
        case "0":
          this.showStatus = false;
          break;
        case "1":
          this.showStatus = false;
          break;
        case "2":
          this.showStatus = false;
          break;
        case "3":
          this.showStatus = false;
          break;
        case "4":
          this.showStatus = false;
          break;
        case "5":
          this.showStatus = true;
          break;
        case "6":
          this.showStatus = true;
          break;
        case "7":
          this.showStatus = true;
          break;
        case "D":
          this.showStatus = true;
          break;
        case "X":
          this.showStatus = false;
          break;

        default:
          break;
      }
    },
    //
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (valid) {
          this.active = 2;
        } else {
          if (this.formData.picture1) {
            //
            this.active = 1;
          } else {
            //
            this.active = 0;
          }
          return false;
        }
        if (this.license1 !== this.license2) {
          this.$alert("", "", {
            confirmButtonText: "",
          });
          return;
        }
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        })
          .then(() => {
            //
            this.Axios.post(`/openapi/dztz/qrcode/saveTransportVehicle`, {
              parkId: this.parkId,
              accessToken: this.accessToken,
              userName: this.userName,
              isobd: this.formData.isobd,
              plateNumber: this.formData.plateNumber,
              vehicleModel: this.formData.vehicleModel, //
              useCharacter: this.formData.useCharacter, //
              registerDate: this.formData.registerDate,
              issueDate: this.formData.issueDate,
              vin: this.formData.vin,
              engineModel: this.formData.engineModel,
              plateColor: this.formData.plateColor,
              vehicleTypeId: this.formData.vehicleTypeId,
              emissionStageId: this.formData.emissionStageId,
              fuelType: this.formData.fuelType,
              owner: this.formData.owner,
              licenseURL: `${this.formData.picture1},${this.formData.picture3}`,
              clzp: this.formData.picture4,
              goodsListURL: this.formData.picture2,
              grossMass: this.formData.grossMass,
              approvedLoad: this.formData.approvedLoad,
              appprovedPassengerCapacity:
                this.formData.appprovedPassengerCapacity,
              address: this.formData.address,
              remarks: this.formData.remarks,
            }).then((data) => {
              if (data.data.code == 200) {
                this.resetForm();
                this.$message({
                  showClose: true,
                  message: "",
                  type: "success",
                  center: true,
                  offset: 400,
                });
                //
                this.active = 3;
              }
            });
          })
          .catch(() => {
            this.$message({
              showClose: true,
              message: "",
              type: "success",
              center: true,
              offset: 400,
            });
          });
      });
    },
    //
    resetForm() {
      this.$refs["elForm"].resetFields();
      //
      this.picture1fileList = [];
      this.picture2fileList = [];
      this.picture3fileList = [];
      this.picture4fileList = [];
      //
      this.active = 0;
    },
    //blobbase64
    getBase64(blob, callback) {
      var a = new FileReader();
      a.onload = function (e) {
        callback(e.target.result);
      };
      a.readAsDataURL(blob);
    },
    //1 
    getFile1(file, fileList) {
      this.ys(file, 1).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/ocrDrivingLicense`, {
            parkId: this.parkId,
            userName: this.userName,
            image: res,
          })
            .then((res) => {
              const data = res.data.data;
              if (!data.plateNumber) {
                return this.$message.error(",");
              }
              this.license1 = data.plateNumber;
              let date =
                data.registerDate && data.registerDate.length == 8
                  ? data.registerDate
                  : "";
              let date2 =
                data.issueDate && data.issueDate.length == 8
                  ? data.issueDate
                  : "";
              if (date != "" && new RegExp(/\d{4}\d{2}\d{2}/).test(date)) {
                this.formData.registerDate =
                  date.slice(0, 4) +
                  "-" +
                  date.slice(4, 6) +
                  "-" +
                  date.slice(6, 8);
              }
              if (date2 != "" && new RegExp(/\d{4}\d{2}\d{2}/).test(date2)) {
                this.formData.issueDate =
                  date2.slice(0, 4) +
                  "-" +
                  date2.slice(4, 6) +
                  "-" +
                  date2.slice(6, 8);
              }
              //
              this.formData.plateNumber = data.plateNumber;
              //
              this.formData.vehicleModel = data.vehicleModel;
              //
              this.formData.useCharacter = data.useCharacter;
              //
              this.formData.vin = data.vin;
              // id id
              const index = this.vehicleTypeOptions
                .map((item) => item.vehicleTypeName)
                .indexOf(data.vehicleTypeName);
              if (index != -1) {
                this.formData.vehicleTypeId =
                  this.vehicleTypeOptions[index].vehicleTypeId;
              }
              //
              this.formData.owner = data.owner;
              //
              this.formData.picture1 = data.imageURL;
              //
              this.formData.engineModel = data.engineModel;
              this.formData.address = data.address;
              //
              this.active = 1;
            })
        });
      });
    },
    //3 
    getFile3(file, fileList) {
      this.ys(file, 3).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/ocrDrivingLicense2`, {
            parkId: this.parkId,
            accessToken: this.accessToken,
            userName: this.userName,
            image: res,
          })
            .then((res) => {
              const data = res.data.data;
              if (!data.license) {
                return this.$message.error(",");
              }
              this.license2 = data.license;
              this.formData.grossMass = data.grossMass.split("kg")[0];
              this.formData.approvedLoad = data.approvedLoad.split("kg")[0];
              this.formData.appprovedPassengerCapacity = getNum(
                data.appprovedPassengerCapacity.split("")[0]
              );
              this.formData.picture3 = data.imageURL;
              this.active = 1;
            })
        });
      });
    },
    //2 
    getFile2(file, fileList) {
      this.ys(file, 2).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId: this.parkId,
            accessToken: this.accessToken,
            userName: this.userName,
            image: res,
          }).then((data) => {
            // 
            if (this.formData.picture2 == "") {
              this.formData.picture2 = data.data.data;
            } else {
              this.formData.picture2 =
                this.formData.picture2 + "," + data.data.data;
            }
          });
        });
      });
    },
    //4 
    getFile4(file, fileList) {
      this.ys(file, 4).then((blob) => {
        this.getBase64(blob, (res) => {
          res = res.replace(/.*;base64,/, "");
          this.Axios.post(`/openapi/dztz/uploadFile`, {
            parkId: this.parkId,
            accessToken: this.accessToken,
            userName: this.userName,
            image: res,
          }).then((data) => {
            // 
            if (this.formData.picture4 == "") {
              this.formData.picture4 = data.data.data;
            } else {
              this.formData.picture4 =
                this.formData.picture4 + "," + data.data.data;
            }
          });
        });
      });
    },
      //1 
    removeFile1(file, fileList) {
      this.picture1fileList = [];
      this.active = 0;
    },
    //2 
    removeFile2(file, fileList) {
      this.formData.picture2 = "";
      this.picture2fileList = [];
    },
     //3 
    removeFile3(file, fileList) {
       this.picture3fileList = [];
      this.active = 0;
    },
    //4 
    removeFile4(file, fileList) {
      this.formData.picture4 = "";
      this.picture4fileList = [];
    },
    //
    pageInit() {
      this.parkId = this.$route.query.parkId;
      this.accessToken = this.$route.query.accessToken;
      this.userName = this.$route.query.userName;
      this.accessPlatform = this.$route.query.accessPlatform;
      //
      this.Axios.post(`/openapi/dztz/getVehicleTypeList`, {
        parkId: this.parkId,
        accessToken: this.accessToken,
        userName: this.userName,
      }).then((data) => {
        this.vehicleTypeOptions = data.data.data;
      });
      //
      this.Axios.post(`/openapi/dztz/getEmissionStageList`, {
        parkId: this.parkId,
        accessToken: this.accessToken,
        userName: this.userName,
      }).then((data) => {
        this.emissionOptions = data.data.data;
      });
    },
    // num 1  2  3  4 
    ys(file, num) {
      return new Promise((resolve) => {
        console.log(file);
        let isLt2M = file.file.size / 1024 / 1024< 2;
        if (isLt2M) {
          resolve(file.file);
        } else {
          imageConversion
             .compressAccurately(file.file, 800*2)
            .then((res) => {
              resolve(res);
            });
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
.box {
  padding: 10px;
}
/deep/.hide .el-upload--picture-card {
  display: none;
}
.el-form-item--medium .el-form-item__content,
.el-form-item--medium /deep/.el-form-item__label {
  line-height: 20px;
}
/deep/.el-upload__tip {
  margin-top: 0;
}
.el-row {
  margin-bottom: 20px;
}
</style>



